import step from '../../../components/step/step.vue'
import infoText from '../../../components/m-info-text/m-info-text'
import mButton from '../../../components/m-button.vue'
import uniIcon from "@/components/uni-icon/uni-icon.vue"
import tButton from '../../../components/twoButton/twoButton'
export default {
	data() {
		return {
			step: {
				selfget:'待提油',
				selfget1:'已提油',
				selfget2:'请确认',
				value1: '待确认',
				value11: '已确认',
				value2: '待发油',
				value22: '已发油',
				value3: '待收油',
				value4: '已拒绝',
				value5: '已完成',
				waitSure: '',
				waitSend: '',
				waitGet: '',
				refuse: 'step-wait',
				complete: 'step-ago',
			},
			confirmed: {
				order: '预约单号',
				time: '提油时间',
				company: '提油单位',
				oil: '油品类型',
				much: '提油数量(吨)',
				main: '提油方式',
			},
			btn: {
				type: 'primary',
				value: '关闭',
				commit:'确认收油',
				commit1:'确认提油'
			},
			disabled: true,
			address: '', //提油地址
			order: '', //预约单号
			company: '', //提油单位
			oil: '', //油的类型
			much: '', //油量
			main: '', //提油方式
			status: '', //预约状态
			right: true,
			rId: '', //reserveId
			oId: '', //orderId
			reserveInfo: '',
			showAddress: false,
			time: '',
		}
	},
	onLoad(option) {
		this.rId = option.reserve_id;
		this.oId = option.no;
		this.getReserveOilList();
	},
	methods: {
		getReserveOilList() {
			this.test.post('order/query_reserve_info', {
				reserve_id: this.rId,
			}).then(res => {
				console.log(res)
				if (res.statusCode == 200 && res.data.errorCode == 0) {
					this.reserveInfo = res.data.value;
					this.status = this.reserveInfo.status;
					this.time = new Date(this.reserveInfo.reserve_time + 8 * 3600 * 1000).toJSON().substr(0, 16).replace('T', ' ').replace(
						/-/g, '-');
					this.order = this.reserveInfo.no
					this.much = String(this.reserveInfo.extract_num);
					this.address = this.reserveInfo.ship_addr;
					this.company = this.reserveInfo.org_name;
					this.oil = this.reserveInfo.oil_type;
					this.main = this.reserveInfo.get_type;
					// if (this.reserveInfo.get_type == 0) {
					// 	this.main = '配送'
					// } else {
					// 	this.main = '自提'
					// };
					if (this.main == '配送') {
						this.showAddress = true
					} else {
						this.showAddress = false
					};
					if (this.status == 1) {
						this.step.waitSure = "step-active"
						this.step.waitSend = ""
						this.step.waitGet = ""
					} else if (this.status == 2 || this.status == 4) {
						this.step.waitSure = "step-ago"
						this.step.waitSend = "step-active"
						this.step.waitGet = ""
					} else if (this.status == 3 || this.status == 5) {
						this.step.waitSure = "step-ago"
						this.step.waitSend = "step-ago"
						this.step.waitGet = "step-active"
					};
					// if (el.status == 1 || el.status == -1 || el.status == 9) {
					// }



				}
			}).catch(err => {
				console.log(err)
			})
		},
		close() {
			uni.redirectTo({
				url: '../reserveOilList'
			})
		},
		back() {
			uni.redirectTo({
				url: '../reserveOilList'
			})
		},
		finish(){
			this.test.post('order/confirm_reserve',{
				id:this.rId
			}).then(res=>{
				console.log(res)
				if(res.statusCode == 200 && res.data.errorCode == 0){
					this.getReserveOilList()
				}
			}).catch(err=>{
				console.log(err)
			})
		},
		getCode() {
			uni.navigateTo({
				url: "oliCode/oliCode?id=" + this.rId + '&no=' + this.order
			})
		},
	},
	components: {
		step,
		infoText,
		mButton,
		uniIcon,
		tButton
	},
}
